import { PageProps } from "gatsby"
import React from "react"
import { useForm } from "react-hook-form"
import Layout from "~/components/layout"
import Seo from "~/components/seo"
import { toast } from "~/components/toast"
import { postVideoInfo, PostVideoInfo } from "~/modules/google-sheets"
import * as styles from "~/styles/pages/add.module.css"

const requiredMessage = ""

type FormData = {
  title: string
  url: string
  startHours: number
  startMinutes: number
  startSeconds: number
  endHours: number
  endMinutes: number
  endSeconds: number
  person: string
}

const padNumber = (value: number): string => {
  return ("" + value).padStart(2, "0")
}

const AddPage: React.FC<PageProps> = ({ location }) => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
    getValues,
    setError,
    clearErrors,
  } = useForm<FormData>()

  const onSubmit = handleSubmit(data => {
    const start = Number.isNaN(data.startHours)
      ? `${data.startMinutes}:${padNumber(data.startSeconds)}`
      : `${data.startHours}:${padNumber(data.startMinutes)}:${padNumber(
          data.startSeconds
        )}`
    const end = Number.isNaN(data.endHours)
      ? `${data.endMinutes}:${padNumber(data.endSeconds)}`
      : `${data.endHours}:${padNumber(data.endMinutes)}:${padNumber(
          data.endSeconds
        )}`

    const createdAt = (() => {
      const result = new Date()
      return `${result.getFullYear()}/${padNumber(
        result.getMonth() + 1
      )}/${padNumber(result.getDate())}`
    })()
    const videoInfo: PostVideoInfo = {
      title: data.title,
      url: data.url,
      start,
      end,
      person: data.person,
      createdAt,
    }
    toast("")
    postVideoInfo(videoInfo)
    reset()
  })

  const timestampNames: (keyof FormData)[] = [
    "startHours",
    "startMinutes",
    "startSeconds",
    "endHours",
    "endMinutes",
    "endSeconds",
  ]
  const timestampErrorMessage =
    ""

  const validateTimestamp = () => {
    const {
      startHours,
      startMinutes,
      startSeconds,
      endHours,
      endMinutes,
      endSeconds,
    } = getValues()
    if (
      Number.isNaN(startMinutes) ||
      Number.isNaN(startSeconds) ||
      Number.isNaN(endMinutes) ||
      Number.isNaN(endSeconds)
    ) {
      return true
    }

    const result =
      (Number.isNaN(startHours) ? 0 : startHours) * 3600 +
        startMinutes * 60 +
        startSeconds <
      (Number.isNaN(endHours) ? 0 : endHours) * 3600 +
        endMinutes * 60 +
        endSeconds

    if (result) {
      clearErrors(timestampNames)
      return true
    }
    timestampNames.forEach(name =>
      setError(name, {
        type: "validate",
        message: timestampErrorMessage,
      })
    )
    return timestampErrorMessage
  }

  return (
    <Layout location={location}>
      <Seo title="" />
      <h2 className={styles.heading}></h2>
      <p>
        <a
          href="https://docs.google.com/spreadsheets/d/1aT4i9__D9y-JOgfmfBxL1TYJSyMmRX_OhGcRr83XL_0/edit?usp=sharing"
          target="_blank"
          rel="nofollow noopener noreferrer"
        >
          
        </a>{" "}
         from Web App
        <br />
        
      </p>
      <p>
        {" "}
        <a
          href="https://creativecommons.org/publicdomain/zero/1.0/deed.ja"
          target="_blank"
          rel="nofollow noopener noreferrer"
        >
          CC0-1.0
        </a>{" "}
        
      </p>
      <form className={styles.form} onSubmit={onSubmit} noValidate>
        <label className={styles.formLabel}>
          <span className={styles.formLabelValue}></span>
          <input
            className={errors.title ? styles.formInputError : styles.formInput}
            {...register("title", { required: requiredMessage })}
          />
          {errors.title && (
            <p className={styles.formInputErrorMessage}>
              {errors.title.message}
            </p>
          )}
        </label>
        <label className={styles.formLabel}>
          <span className={styles.formLabelValue}>URL</span>
          <input
            type="url"
            placeholder="https://www.youtube.com/watch?v="
            className={errors.url ? styles.formInputError : styles.formInput}
            {...register("url", {
              required: requiredMessage,
              pattern: {
                value: /https:\/\/(www|m)\.youtube\.com\/watch\?v=.+/,
                message: "YouTube URL",
              },
            })}
          />
          {errors.url && (
            <p className={styles.formInputErrorMessage}>{errors.url.message}</p>
          )}
        </label>
        <label className={styles.formLabel}>
          <span className={styles.formLabelValue}></span>
          <span className={styles.formInputMultipleContainer}>
            <input
              type="number"
              min={0}
              className={
                errors.startHours ? styles.formInputError : styles.formInput
              }
              {...register("startHours", {
                valueAsNumber: true,
                min: {
                  value: 0,
                  message: "0",
                },
                validate: validateTimestamp,
              })}
            />
            <span className={styles.formInputMultipleSeparator}>:</span>
            <input
              type="number"
              max={59}
              min={0}
              className={
                errors.startMinutes ? styles.formInputError : styles.formInput
              }
              {...register("startMinutes", {
                required: "",
                valueAsNumber: true,
                max: {
                  value: 59,
                  message: "59",
                },
                min: {
                  value: 0,
                  message: "0",
                },
                validate: validateTimestamp,
              })}
            />
            <span className={styles.formInputMultipleSeparator}>:</span>
            <input
              type="number"
              max={59}
              min={0}
              className={
                errors.startSeconds ? styles.formInputError : styles.formInput
              }
              {...register("startSeconds", {
                required: "",
                valueAsNumber: true,
                max: {
                  value: 59,
                  message: "59",
                },
                min: {
                  value: 0,
                  message: "0",
                },
                validate: validateTimestamp,
              })}
            />
          </span>
          {errors.startHours && errors.startHours.type !== "validate" && (
            <p className={styles.formInputErrorMessage}>
              {errors.startHours.message}
            </p>
          )}
          {errors.startMinutes && errors.startMinutes.type !== "validate" && (
            <p className={styles.formInputErrorMessage}>
              {errors.startMinutes.message}
            </p>
          )}
          {errors.startSeconds && (
            <p className={styles.formInputErrorMessage}>
              {errors.startSeconds.message}
            </p>
          )}
        </label>
        <label className={styles.formLabel}>
          <span className={styles.formLabelValue}></span>
          <span className={styles.formInputMultipleContainer}>
            <input
              type="number"
              min={0}
              className={
                errors.endHours ? styles.formInputError : styles.formInput
              }
              {...register("endHours", {
                valueAsNumber: true,
                min: {
                  value: 0,
                  message: "0",
                },
                validate: validateTimestamp,
              })}
            />
            <span className={styles.formInputMultipleSeparator}>:</span>
            <input
              type="number"
              max={59}
              min={0}
              className={
                errors.endMinutes ? styles.formInputError : styles.formInput
              }
              {...register("endMinutes", {
                required: "",
                valueAsNumber: true,
                max: {
                  value: 59,
                  message: "59",
                },
                min: {
                  value: 0,
                  message: "0",
                },
                validate: validateTimestamp,
              })}
            />
            <span className={styles.formInputMultipleSeparator}>:</span>
            <input
              type="number"
              max={59}
              min={0}
              className={
                errors.endSeconds ? styles.formInputError : styles.formInput
              }
              {...register("endSeconds", {
                required: "",
                valueAsNumber: true,
                max: {
                  value: 59,
                  message: "59",
                },
                min: {
                  value: 0,
                  message: "0",
                },
                validate: validateTimestamp,
              })}
            />
          </span>
          {errors.endHours && errors.endHours.type !== "validate" && (
            <p className={styles.formInputErrorMessage}>
              {errors.endHours.message}
            </p>
          )}
          {errors.endMinutes && errors.endMinutes.type !== "validate" && (
            <p className={styles.formInputErrorMessage}>
              {errors.endMinutes.message}
            </p>
          )}
          {errors.endSeconds && (
            <p className={styles.formInputErrorMessage}>
              {errors.endSeconds.message}
            </p>
          )}
        </label>
        <label className={styles.formLabel}>
          <span className={styles.formLabelValue}></span>
          <select
            className={
              errors.person
                ? styles.formInputSelectError
                : styles.formInputSelect
            }
            {...register("person", { required: requiredMessage })}
          >
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          {errors.person && (
            <p className={styles.formInputErrorMessage}>
              {errors.person.message}
            </p>
          )}
        </label>
        <button className={styles.formSubmit} type="submit">
          
        </button>
      </form>
    </Layout>
  )
}

export default AddPage
